:root {
  --link-color: #00acdc;
}

html {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

a {
  color: #000;
  height: 60px;
  text-decoration: none;
  display: block;
}

.nav-logo a {
  align-items: center;
  display: flex;
}

img {
  width: 40px;
  height: 40px;
}

.nav {
  align-items: center;
  height: 60px;
  display: flex;
}

.nav__list {
  justify-content: flex-end;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  text-align: center;
  height: 60px;
  margin-left: 20px;
  line-height: 60px;
}

.nav__item:first-child {
  margin-left: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  transition: transform .15s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active:hover:after {
  transform: scaleX(1);
}

a:hover {
  color: var(--link-color);
}
